import Error from "../error/Error";
import { Link } from "react-router-dom";

const Page404 = () => {
    console.log('page 404');
    const returnSymbol = '\u00AB';
    return (
        <div>
            <Error style={{width: '100%'}}/>
            <p style={{'textAlign': 'center', 'fontSize': '28px', 'fontWeight': '700'}}>This page doesn't exist</p>
            <Link style={{'color': '#9F0013', 'display': 'flex', 'justifyContent': 'center', 'alignItems': 'center', 'fontSize': '36px', 'fontWeight': '700', 'marginTop': '50px', }} to='/'>{returnSymbol} Back to main page</Link>
        </div>
    )
}

export default Page404;